export default {
	data() {
		return {
			selectIndex: 0,
			loadingPage:true,
			lineItems: [],
			processItems: [
				[
					{
						title: '销售订单下达',
						status: 2,
						name: 'pony马',
						code: '10000056',
						date: '2020-05-06'
					},
					{
						title: '发运',
						status: 1,
						items: [{statusName: '未确认',status: 2,name: '王小明',date: '2020-09-30'},{statusName: '在途',status: 1,name: '张三',date: '2020-09-30',
						linkName:'发运轨迹'}]
					},
				],
				[
					{
						title: '销售订单下达',
						status: 2,
						name: 'pony马',
						code: '10000056',
						date: '2020-05-06'
					},
					{
						title: '销售订单下达2',
						status: 2,
						name: 'pony马',
						code: '10000056',
						date: '2020-05-06'
					}
				],
				[],
				[],
				[],
				[]
			],
			requestUrl:'app/home/order/process/detail/',
			salesOrderNo: '',
			posnr: '',
			data:{}
		}
	},
	onLoad(option) {
		this.salesOrderNo = option.salesOrderNo
		this.posnr = option.posnr
		this.getData()
	},
	methods: {
		getData() {
			var self = this
			uni.showLoading({
				title:'加载中'
			})
			this.ajax({
				url:this.requestUrl+ this.salesOrderNo+'/'+ this.posnr,
				data: {},
				success:res=>{
					console.log(res)
					if(res.code == 0) {
						self.getDataSuccess(res.data)
					}
				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		getDataSuccess(data) {
			var self = this
			this.loadingPage = false
			this.data = data
			this.lineItems = data.nodes
			this.processItems = []
			data.nodes.forEach(item => {
				self.processItems.push(item.children)
			})
		},
		changeTab(item,index){
			item.active = true;
			this.selectIndex = index
		}
	}
}
